<template>
  <div class="advertising">
    <van-button class="button" plain hairline type="info" @click="jump"
      >跳过广告{{ timeout }}</van-button
    >
    <img class="auto-img" src="../assets/advertising.png" alt="" />
  </div>
</template>

<script>
export default {
  name: "Advertising",
  data() {
    return {
      timeout: 3,
      t1: null,
      t2: null,
    };
  },
  created() {
    if (!this.isLogin) {
      this.$router.push({ name: "Login" });
      return;
    }
    this.timeOut();
    this.advertising();
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  methods: {
    //三秒后跳转
    advertising() {
      this.t2 = setTimeout(() => {
        clearInterval(this.t1);
        this.$router.push({ name: "Menu" });
      }, 3000);
    },
    //倒计时
    timeOut() {
      this.t1 = setInterval(() => {
        this.timeout--;
      }, 1000);
    },
    //跳过
    jump() {
      clearInterval(this.t1);
      clearTimeout(this.t2);
      this.$router.push({ name: "Menu" });
    },
  },
};
</script>

<style lang="less" scoped>
.advertising {
  .button {
    float: right;
  }
}
</style>